<template>
  <div class="results">
    <h2 class="sidebar-title">
      Результаты поиска: {{ results.length }} найдено
    </h2>
    <div class="results-content">
      <router-link to="/main/search">
        <div class="back">
          <div class="back-arrow">
            <img src="@/assets/img/arrow.svg" />
          </div>
          <p class="back-subtitle">Назад</p>
        </div>
      </router-link>
      <portal to="popup-card">
        <div class="card" v-if="card.ind != null" v-show="card.active">
          <div class="card-close" @click="onCardClose()">
            <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M11.625 1.982L10.018.375 6 4.393 1.982.375.375 1.982 4.393 6 .375 10.018l1.607 1.607L6 7.607l4.018 4.018 1.607-1.607L7.607 6l4.018-4.018z" fill="#476D70"/></svg>
          </div>
          <div class="card-title">Информация по объекту</div>
          <div class="card-img">
            <img :src="cardData.previewPath" alt="" />
          </div>
          <div class="card-table__wrapper">
            <table class="card-table">
              <thead>
                <tr>
                  <th>Характеристика</th>
                  <th>Значение</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>Идентификатор</td>
                  <td>{{ cardData.name }}</td>
                </tr>
                <tr>
                  <td>Виток</td>
                  <td>{{ cardData.round }}</td>
                </tr>
                <tr>
                  <td>Маршрут</td>
                  <td>{{ cardData.route }}</td>
                </tr>
                <!-- <tr>
                  <td>Аппарат</td>
                  <td>{{ cardData.scName }}</td>
                </tr> -->
                <tr>
                  <td>Дата съемки</td>
                  <td>{{ cardData.date }}</td>
                </tr>
                <tr>
                  <td>Облачность</td>
                  <td>{{ cardData.cloudiness }}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="card-buttons">
            <button
              class="button button-g card-button"
              @click="
                onPolygonButtonClick(card.ind, cardData.id, cardData.geography)
              "
            >
              Скрыть контур
            </button>
            <button
              class="button button-white card-button"
              type="white"
              @click="
                onImageButtonClick(
                  card.ind,
                  cardData.id,
                  cardData.previewPath,
                  cardData.geography.bbox
                )
              "
            >
              Показать изображение
            </button>
          </div>
        </div>
      </portal>

      <div class="results-wrapper">
        <app-table class="results-table">
          <thead>
            <tr>
              <th></th>
              <th>Идентификатор</th>
              <th>Виток</th>
              <th>Маршрут</th>
              <th>Аппарат</th>
              <th>Дата съемки</th>
              <th>Облачность</th>
              <th></th>
            </tr>
          </thead>
          <tbody v-if="loaded">
            <tr  v-for="(item, i) in results" :key="i">
              <td class="results-table__buttons">
                <button
                  class="button button-white button-small"
                  :class="results[i].polygonActive ? 'active' : ''"
                  @click="onPolygonButtonClick(i, item.id, item.geography)"
                >
                  <svg width="18" height="18" viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:class="'icon icon-vector-o'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><rect x="2.76" y="2.76" width="8.64" height="8.515" rx="2" stroke="#476D70"/><path d="M6.6 12.168v.892a2 2 0 002 2h4.64a2 2 0 002-2V8.545a2 2 0 00-2-2h-.88" stroke="#476D70"/></svg>
                </button>
                <button
                  class="button button-white button-small"
                  :class="results[i].imageActive ? 'active' : ''"
                  @click="
                    onImageButtonClick(
                      i,
                      item.id,
                      item.previewPath,
                      item.geography == null
                        ? null
                        : item.geography.bbox
                    )
                  "
                >
                  <svg width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:class="'icon icon-img'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M1.167 3.5A2.333 2.333 0 013.5 1.167h7A2.333 2.333 0 0112.834 3.5v7a2.333 2.333 0 01-2.334 2.334h-7A2.333 2.333 0 011.167 10.5v-7z" stroke="#476D70" stroke-linecap="round" stroke-linejoin="round"/><path d="M4.958 6.417a1.458 1.458 0 100-2.917 1.458 1.458 0 000 2.917zM8.473 7.362L3.5 12.833h7.078a2.256 2.256 0 002.255-2.255V10.5c0-.272-.102-.376-.286-.577l-2.35-2.564a1.167 1.167 0 00-1.723.003v0z" stroke="#476D70" stroke-linecap="round" stroke-linejoin="round"/></svg>
                </button>
              </td>
              <td>{{ item.name }}</td>
              <td>{{ item.round }}</td>
              <td>{{ item.route }}</td>
              <td>Аппарат</td>
              <td>{{ item.date }}</td>
              <td>{{ item.cloudiness }}</td>
              <td class="results-table__buttons">
                <button
                  class="button button-white button-small"
                  :class="results[i].cardActive ? 'active' : ''"
                  @click="onCardButtonClick(i)"
                >
                  <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:class="'icon icon-open'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M4.5 3A1.5 1.5 0 003 4.5v7A1.5 1.5 0 004.5 13h7a1.5 1.5 0 001.5-1.5V9.27a.5.5 0 011 0v2.23a2.5 2.5 0 01-2.5 2.5h-7A2.5 2.5 0 012 11.5v-7A2.5 2.5 0 014.5 2h2.23a.5.5 0 110 1H4.5zm4.27-.5a.5.5 0 01.5-.5h4.23a.5.5 0 01.5.5v4.23a.5.5 0 01-1 0V3.708L9.623 7.084a.5.5 0 11-.707-.707L12.293 3H9.269a.5.5 0 01-.5-.5h.001z" fill="#476D70"/></svg>
                </button>
              </td>
            </tr>
          </tbody>
        </app-table>
      </div>
    </div>
    <!-- <vs-pagination :total-pages="5"></vs-pagination> -->
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import AppTable from "@/components/table/AppTable";
// import VsPagination from "@vuesimple/vs-pagination";
export default {
  components: {
    AppTable,
    // VsPagination,
  },
  data() {
    return {
      loaded: false,
      buttons: [],
      card: {
        active: false,
        ind: null,
      },
    };
  },
  computed: {
    ...mapGetters("results", {
      results: "getResults",
    }),
    cardData() {
      if (this.card.ind != null) {
        return this.results[this.card.ind];
      } else {
        return null;
      }
    },
  },
  methods: {
    ...mapActions("map", [
      "addGeoJsonPolygon",
      "removeGeoJsonPolygon",
      "addImage",
      "removeImage",
    ]),
    ...mapActions("results", ['setResultProperty']), 
    onPolygonButtonClick(ind, id, json) {
      if (this.results[ind].polygonActive) {
        this.removeGeoJsonPolygon(id);
        this.setResultProperty({index: ind, property: 'polygonActive', value: false});
      } else {
        this.addGeoJsonPolygon({ id, json });
        this.setResultProperty({index: ind, property: 'polygonActive', value: true});
      }
    },
    onImageButtonClick(ind, id, img, bounds) {
      if (this.results[ind].imageActive) {
        this.removeImage(id);
        this.setResultProperty({index: ind, property: 'imageActive', value: false});
      } else {
        this.addImage({ id, img, bounds });
        this.setResultProperty({index: ind, property: 'imageActive', value: true});
      }
    },
    onCardButtonClick(ind) {
      if (this.results[ind].cardActive) {
        this.card.active = false;
        this.setResultProperty({index: ind, property: 'cardActive', value: false});
      } else {
        this.card.ind = ind;
        for (let i = 0; i < this.results.length; i++) {
          this.setResultProperty({index: i, property: 'cardActive', value: false});
        }
        this.setResultProperty({index: ind, property: 'cardActive', value: true});
        this.card.active = true;
        this.card.data = { ...this.results[ind] };
      }
    },
    onCardClose() {
      for (let i = 0; i < this.results.length; i++) {
          this.setResultProperty({index: i, property: 'cardActive', value: false});
        }
      this.card.active = false;
    },
  },
  created() {
      this.loaded = true;
  },
};
</script>

<style lang="scss">
.card {
  z-index: 11;
  position: absolute;
  width: 300px;
  background: #000;
  top: 20px;
  right: 20px;
  background: $gradient-w;
  box-shadow: $shadow-big;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px 20px;
  &-close {
    position: absolute;
    right: 5px;
    top: 5px;
    width: 25px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    svg path {
      transition: all 0.1s ease-out;
    }
    &:hover {
      svg path {
        fill: $color-main;
      }
    }
  }
  &-title {
    font-size: 16px;
    color: #000;
    margin-bottom: 15px;
  }
  &-img {
    width: 200px;
    margin-bottom: 20px;
    border: 1px solid #000;
  }
  &-table {
    &__wrapper {
      flex: 1;
    }
    color: #000;
    width: 100%;
    border-collapse: collapse;
    table-layout: fixed;
    th {
      color: $color-main-dark;
      text-align: left;
      font-size: 12px;
    }
    td {
      word-break: break-all;
      padding: 10px 10px 10px 0;
      font-size: 10px;
    }
    tr {
      border-bottom: 1px solid rgba($color-main, 0.2);
    }
  }
  &-buttons {
    display: flex;
    flex-direction: column;
    align-items: stretch;
  }
  &-button {
    margin-top: 15px;
  }
}
.results {
  &-content {
    padding: 10px 30px;
  }
  .back {
    margin-bottom: 0px;
    display: flex;
    align-items: center;
    &-arrow {
      width: 30px;
    }
    p {
      margin-left: 10px;
      color: $color-main-dark;
    }
  }
  &-wrapper {
    background: #ffffff;
    box-shadow: $shadow-big;
    border-radius: 10px;
    overflow: hidden;
  }
  &-table {
    td,
    th {
      word-break: break-all;
    }
    * {
      font-size: 10px !important;
    }
    &__buttons {
      padding: 10px !important;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  .button-small {
    margin: 0 5px;
    width: 25px;
    height: 25px;
    padding: 0;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    &.active {
      background: $gradient;
      svg * {
        stroke: #fff;
      }
      .icon-open {
        path {
          fill: #fff;
          stroke: none;
        }
      }
    }
    &:first-child {
      margin-left: 0;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  .icon {
    width: 15px;
    height: 15px;
    &-vector-o {
      width: 20px;
      height: 20px;
    }
  }
}
</style>